body {
  margin: 0;
  font-family: 'Segoe UI', sans-serif;
  background-color: #f4f4f4;
}

.content {
  padding: 2rem;
  min-height: 80vh;
}

h1 {
  color: #004d00;
}

footer {
  background-color: #ddd;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
}


.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.gallery-grid img {
  width: 100%;
  border-radius: 8px;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.gallery-grid img:hover {
  transform: scale(1.05);
}

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.lightbox-content {
  background: white;
  padding: 1rem;
  border-radius: 8px;
  text-align: center;
  max-width: 90%;
}

.lightbox-content img {
  max-width: 100%;
  max-height: 70vh;
  margin-bottom: 1rem;
}

.lightbox-content button {
  background-color: #017401;
  color: white;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 6px;
}


